// -----------------------------------------------------------------------------
// 		Second row : OVERVIEW
// -----------------------------------------------------------------------------

.node-school {
	.field-item {
		.overview-wrap {
			background: #f0f0f0;
			background: -moz-linear-gradient(top,  #f0f0f0 0%, #ffffff 100%);
			background: -webkit-linear-gradient(top,  #f0f0f0 0%,#ffffff 100%);
			background: linear-gradient(to bottom,  #f0f0f0 0%,#ffffff 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f0f0f0', endColorstr='#ffffff',GradientType=0 );

			.overview {
				.section-head {

				}
				.section-body {
					.feature {
						@media #{$md-min} {
							padding-right: 5%;
						}
						ul {
							list-style: none;
							li {
								&:before {
									content: '●';
									position: relative;
									display: inline-block;
									height: 0;
									width: 0;
									color: #007DB3;
									top: 2px;
									left: -18px;
								}
							}
						}
					}
					.video-wrapper {
						padding: 10px 0;
						@media #{$xs-max} {
							margin-left: -15px;
							margin-right: -15px;
						}
						.video {
							.wistia_responsive_padding {
								padding-top: 56.25%;
							}
						}
					}
				}
			}
		}
	}
}










